:root {

  //size
  --fz-main: 1em;

  --fz-128: 128px;
  --fz-81: 81px;

  --fz-70: 70px;
  --fz-64: 64px;
  --fz-48: 48px;

  --fz-32: 32px;
  --fz-28: 28px;
  --fz-24: 24px;
  --fz-20: 20px;

  --fz-18: 18px;

  --fz-16: 16px;
  --fz-14: 14px;
  --fz-12: 12px;
  --fz-10: 10px;
  --fz-9: 9px;
  --fz-7: 7px;

  //text-color
  --text-main-color: #101010;
  --nav-link-color: #ffffff;
  --white-color: #ffffff;
  --grey-color: #D6D6D6;
  --light-grey-color: #797979;
  --darken-color: #161616;
  --black-color: #000000;
  --light-grey-program: #C5C5C5;
  --bg-btn-color: #FF0074;
  --dark-test-color: #101010;

  //--bg-color
  --bg-main-color: #101010;
  --bg-white-color: #ffffff;

  --bg-darken-color: #161616;





  @media (max-width:1600px) {
    --fz-main: calc(1em * 0.7);
  }

  @media (max-width:992px) {
    --fz-main: calc(1em * 0.6);
  }

  @media (max-width:576px) {
    --fz-main: calc(1em * 1);
  }

  @media (min-width:2200px) {
    --fz-main: calc(1em * 1.4);
  }

  @media (min-width:2900px) {
    --fz-main: calc(1em * 1.9);
  }


}







@mixin breakpoint($point) {
  @if $point==exxl {
    @media (max-width:1742px) {
      @content;
    }
  }

  @else if $point==fourk {
    @media (min-width:2400px) {
      @content;
    }
  }

  @else if $point==xxl {
    @media (max-width:1600px) {
      @content;
    }
  }

  @else if $point==xl {
    @media (max-width:1537px) {
      @content;
    }
  }

  @else if $point==elg {
    @media (max-width:1367px) {
      @content;
    }
  }

  @else if $point==lg {
    @media (max-width:1280px) {
      @content;
    }
  }

  @else if $point==md {
    @media (max-width:992px) {
      @content;
    }
  }

  @else if $point==emd {
    @media (max-width:768px) {
      @content;
    }
  }

  @else if $point==sm {
    @media (max-width:576px) {
      @content;
    }
  }

  @else if $point==esm {
    @media (max-width:412px) {
      @content;
    }
  }
}



@mixin font-settings($size: false, $lh: false, $weight: false, $color: false, ) {
  @if $size {
    font-size: $size;
  }

  @if $lh {
    line-height: $lh;
  }

  @if $weight {
    font-weight: $weight;
  }

  @if $color {
    color: $color;
  }
}



