.partners {
  padding: 128px 0;

  @include breakpoint(sm) {
    padding: 60px 0;
  }

  // .partners__title

  &__title {
    @include font-settings(var(--fz-64), 1, 800, var(--white-color), );
    margin-bottom: 96px;
    max-width: 650px;

    @include breakpoint(sm) {
      font-size: var(--fz-32);
      margin-bottom: 48px;
    }

  }

  // .partners__logo

  &__logo {}

  // .partners__logo-container

  &__logo-container {
    max-width: 1720px;
    display: flex;
    align-items: center;
    gap: 48px;
    justify-content: center;

    @include breakpoint(lg) {
      padding: 0 19px;
      gap: 30px;
    }

    @include breakpoint(emd) {
      justify-content: center;
      flex-wrap: wrap;
    }


    &:nth-child(1) {
      margin: 0 auto 48px;

      @include breakpoint(lg) {
        margin: 0 auto 30px;
      }
    }

    &:nth-child(2) {
      margin-left: auto;
      margin-bottom: 48px;

      @include breakpoint(lg) {
        margin: 0 auto 30px;
      }
    }

    &:nth-child(3) {
      margin: 0 auto;
    }
  }

  // .partners__img

  &__img {
    filter: grayscale(1) brightness(0.5);
    transition: filter 0.3s ease-in-out;

    @include breakpoint(emd) {
      flex-basis: 45%;
    }

    &:hover {
      filter: grayscale(0) drop-shadow(2px 4px 6px black);

    }

    img {
      width: 394px;
      height: 192px;

      @include breakpoint(emd) {
        width: auto;
        height: auto;
      }
    }
  }
}

.no-filter {
  filter: grayscale(0) brightness(1) !important;
}