.footer {
  padding-bottom: 295px;
  position: relative;

  @include breakpoint(emd) {
    padding-bottom: 50px;
  }

  &-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  // .footer__logo

  &__logo {
    img {
      max-width: 188px;

      @include breakpoint(sm) {
        width: 125px;
      }
    }
  }

  a {
    @include font-settings(var(--fz-20), 1, 400, var(--white-color), );

    @include breakpoint(sm) {
      font-size: var(--fz-12);
    }
  }

  .radial-bottom {
    width: 44rem;
    height: 15rem;
    right: -16%;
    left: auto;
    position: absolute;
    bottom: 0;
    background: radial-gradient(circle, rgb(251, 16, 187) 0%, rgb(251, 16, 116) 100%);

    filter: blur(11rem);
    z-index: -1;


    @include breakpoint(sm) {
      width: 60vw;
      height: 30vw;
      right: 0%;
      left: auto;
      position: absolute;
      bottom: 0;
      background: radial-gradient(circle, rgb(251, 16, 187) 0%, rgb(251, 16, 116) 100%);
      filter: blur(75px);
      z-index: -1;
    }



  }

}