.calender__btn {
  display: flex;
  align-items: center;
  gap: 10px;
  border: 1px solid var(--light-grey-program);
  border-radius: 64px;
  padding: 1.5rem 2rem;
  @include font-settings(var(--fz-20), 1, 500, var(--light-grey-color), );

  @include breakpoint(sm) {
    font-size: var(--fz-16);
  }
}

.btn-controls {
  display: flex;
  align-items: center;
  gap: 32px;
  padding-bottom: 128px;
  justify-content: center;

  @include breakpoint(sm) {
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
    padding-bottom: 68px;
  }
}

.program {
  background: #FFFFFF;
  border-radius: 64px;
  padding-top: 118px;
  position: relative;
  z-index: 2;

  @include breakpoint(sm) {
    border-radius: 32px;
    padding-top: 68px;
  }

  &__btn {
    @include font-settings(var(--fz-20), 1, 500, var(--nav-link-color), );

    @include breakpoint(sm) {
      font-size: var(--fz-16);
    }
  }

  &__content-border {
    border-bottom: 1px solid var(--light-grey-program);
    padding-bottom: 48px;

    .program__info-text {
      margin-bottom: 16px;
    }
  }

  // .program__wrap

  &__wrap {
    display: flex;
    justify-content: space-between;
    gap: 20px;

    @include breakpoint(md) {
      flex-direction: column;
    }
  }

  // .program__title

  &__title {
    @include font-settings(var(--fz-64), 1.1, 800, );
    flex-basis: 25%;

    @include breakpoint(xl) {
      font-size: var(--fz-48);
    }

    @include breakpoint(sm) {
      font-size: var(--fz-32);
      margin-bottom: 0;
    }
  }

  // .program__info

  &__info {
    flex-basis: 65%;
  }

  // .program__block

  &__block {}

  &__info-text {
    @include font-settings(var(--fz-20), 1.1, 600, var(--darken-color), );
    max-width: 640px;
    margin-bottom: 1rem;

  }

  // .program__schedule
  &__schedule-start {
    padding-bottom: 83px;
    display: flex;
    align-items: center;
    gap: 45px;
    border-bottom: 1px solid var(--light-grey-color);

    @include breakpoint(sm) {
      flex-direction: column;
      align-items: flex-start;
      padding: 48px 0;
      gap: 22px;
    }
  }



  &__schedule-last {
    padding: 96px 0;
    display: flex;
    align-items: center;
    gap: 2.8125rem;

    @include breakpoint(sm) {
      flex-direction: column;
      align-items: flex-start;
      padding: 48px 0;
      gap: 22px;
    }
  }

  &__schedule {
    display: flex;
    padding-bottom: 91px;
    padding-top: 95px;
    gap: 45px;
    align-items: flex-start;
    border-bottom: 1px solid var(--light-grey-color);

    &:nth-child(4) {
      padding-bottom: 0;
    }

    @include breakpoint(sm) {
      flex-direction: column;
      padding: 48px 0;
      gap: 22px;
    }
  }

  // .program__time

  &__time {
    @include font-settings(var(--fz-48), 1, 300, var(--light-grey-program));

  }

  &__content-wrap {
    align-self: end;

    width: 100%;
  }

  &__time-mob {
    @include breakpoint(sm) {
      display: flex;
      flex-direction: row-reverse;
      gap: 30px;
      align-items: center;
    }

    &:before {
      @include breakpoint(sm) {
        content: '';
        background-image: url('./../images/sign_coffee.svg');
        background-size: cover;
        display: block;
        width: 90px;
        height: 70px;
      }
    }
  }


  // .program__text

  &__text {
    @include font-settings(var(--fz-24), 1, 500, var(--light-grey-color));
    display: flex;
    align-items: center;

    @include breakpoint(sm) {
      font-size: var(--fz-20);
      gap: 8px;
    }

    span {
      background-image: url('./../images/bracket.svg');
      width: 16px;
      height: 60px;
      background-size: cover;
    }

    span:nth-child(2) {
      transform: rotate(180deg);
    }
  }

  // .program__unstarred

  &__unstarred {
    font-family: 'Caveat', sans-serif;
    @include font-settings(var(--fz-48), 1.1, 400, var(--bg-btn-color));

    @include breakpoint(sm) {
      display: none;
    }

    img {
      width: 111px;
      height: 90px;

      @include breakpoint(sm) {
        width: 90px;
        height: 70px;
      }
    }

  }

  // .program__text-title

  &__text-title {
    @include font-settings(var(--fz-32), 1.2, 700, var(--dark-test-color));
    margin-bottom: 35px;
    text-transform: uppercase;

    @include breakpoint(sm) {
      font-size: var(--fz-20);
      margin-bottom: 25px;
    }
  }

  // .program__moderator

  &__moderator {
    border: 1px solid var(--light-grey-program);
    border-radius: 48px;
    padding: 10px 32px;
    display: inline-block;
    @include font-settings(var(--fz-16), 1, 400, );
    // font-size/ line-height/ weight/ color

    @include breakpoint(sm) {
      padding: 10px 22px;
      font-size: var(--fz-14);
    }
  }

  &__group-block {}
}


.group {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-top: 48px;

  @include breakpoint(md) {
    flex-direction: column;
    align-items: flex-start;
    // margin-top: 20px;
  }

  // .group__wrap



  // .group__name

  &__name {
    @include font-settings(var(--fz-16), 1, 700, var(--darken-color), );
    // text-decoration-line: underline;
  }

  &__main-wrap {
    display: flex;
    gap: 32px;

    @include breakpoint(sm) {
      flex-direction: column;
      gap: 20px;
    }
  }

  // .group__company

  &__company {
    @include font-settings(var(--fz-16), 1, 400, var(--darken-color), );
  }




  &__photo-wrap {
    position: relative;

    @include breakpoint(md) {
      flex-direction: column;
      margin-top: 20px;
    }

    img {
      width: 72px;
      height: 72px;
      z-index: 2;
      position: relative;

      &:nth-child(2) {
        z-index: 1;

        @include breakpoint(sm) {
          left: -13px;
        }
      }
    }
  }


}

.group__photo-wrap-img {
  left: 15px;

  @include breakpoint(sm) {
    left: 0;
  }
}

.envelope1 {
  position: relative;

  &:after {
    content: '';
    background-image: url('./../images/envelope1.svg');
    width: 161px;
    height: 6px;
    background-size: cover;
    display: block;
    position: absolute;
    left: 0;

    @include breakpoint(sm) {
      width: 100px;
      background-size: contain;
      background-repeat: no-repeat;
    }

    @include breakpoint(esm) {
      bottom: -2px;
    }
  }
