.info {
  color: var(--white-color);
  padding: 128px 0 96px;


  @include breakpoint(emd) {
    padding: 68px 0;
    border-radius: 32px;
  }

  // .info__main-text

  &__main-text {
    @include font-settings(var(--fz-32), 1.5, 400);

    max-width: 1500px;
    margin: 25px auto 93px;

    @include breakpoint(sm) {
      margin: 0 auto 53px;
      font-size: var(--fz-20);
    }
  }

  // .info__wrap-grid

  &__wrap-grid {
    display: flex;
    align-items: stretch;
    // margin-bottom: 26px;
    gap: 49px;




    @include breakpoint(emd) {
      gap: 25px;
    }

    @include breakpoint(sm) {
      flex-direction: column;
      flex-wrap: wrap;
      align-items: center;
      margin: 0 auto 50px;
    }
  }

  // .info__grid-item

  &__grid-item {
    background: #FFFFFF;
    border-radius: 32px;
    padding: 40px 40px 32px 40px;
    flex-basis: 33%;


    @include breakpoint(emd) {
      padding: 32px 20px 32px 20px;
    }

    @include breakpoint(sm) {
      width: 90%;
    }
  }

  // .info__grid-title

  &__grid-title {
    @include font-settings(var(--fz-20), 1.1, 600, var(--black-color), );
    height: 72px;
    text-align: left;
    margin-bottom: 32px;

    @include breakpoint(emd) {
      height: max-content;
    }

  }

  // .info__grid-img

  &__grid-img {

    img {
      margin: 0 auto;
      display: block;
    }


  }
}

.info.info-bottom {
  @include breakpoint(sm) {
    padding-bottom: 50px;
  }

  .info__grid-item {

    &:nth-child(2) {
      padding-bottom: 0;
    }




    @include breakpoint(emd) {
      padding: 32px 20px 32px 20px;
    }

  }
}