.hero {
  padding-bottom: 105px;
  position: relative;

  // .hero__wrap
  @include breakpoint(sm) {
    padding-bottom: 70px;
  }

  &__wrap {
    color: var(--white-color);
    display: flex;
    justify-content: space-between;
    gap: 40px;
    align-items: center;


    @include breakpoint(emd) {
      flex-direction: column
    }
  }


  // .hero__description

  &__description {
    @include breakpoint(emd) {
      flex-basis: 90%;
      text-align: center;
    }
  }

  // .hero__deadline

  &__deadline {
    @include font-settings(var(--fz-20), 1, 400);
    display: flex;
    gap: 48px;
    margin-bottom: 48px;
    margin-top: 29px;

    @include breakpoint(emd) {
      justify-content: center;
    }

    @include breakpoint(sm) {
      margin: 0 auto 30px;
      gap: 20px;
      justify-content: flex-start;
    }
  }

  // .hero__date

  &__date {
    border: 1px solid var(--bg-white-color);
    border-radius: 32px;
    padding: 14px 19px;


    @include breakpoint(sm) {
      padding: 10px 15px;
      font-size: var(--fz-14);
    }

  }

  // .hero__status

  &__status {
    border: 1px solid var(--bg-white-color);
    border-radius: 32px;
    padding: 14px 26px;

    @include breakpoint(sm) {
      padding: 10px 15px;
      font-size: var(--fz-14);
    }

    svg {
      width: 11px;
      height: 11px;
    }

    span {
      padding-left: 10px;
    }

  }

  // .hero__info

  &__info {
    @include font-settings(var(--fz-32), 1.4, 400);

    @include breakpoint(sm) {
      font-size: var(--fz-20);
      text-align: left;
    }
  }

  // .hero__title

  &__title {
    @include font-settings(var(--fz-128), 1.1, 800);
    max-width: 600px;
    margin-bottom: 8px;

    @include breakpoint(emd) {
      max-width: 100%;
    }

    @include breakpoint(sm) {
      font-size: var(--fz-81);
      text-align: left;
      margin-bottom: 20px;
    }
  }

  // .hero__sub

  &__subtitle {
    @include font-settings(var(--fz-20), 1, 400, var(--grey-color), );
    margin-bottom: 62px;

    @include breakpoint(sm) {
      font-size: var(--fz-16);
      margin-bottom: 52px;
      text-align: left;
    }
  }

  // .hero__btn

  &__btn {
    color: inherit;
    @include font-settings(var(--fz-20), 1, 400, );
    margin-bottom: 48px;
    display: inline-block;

    @include breakpoint(sm) {
      font-size: var(--fz-16);
      margin-bottom: 24px;
      margin-left: 0;
      display: block;
      max-width: 238px;
      margin-left: 0;
    }


  }

  // .hero__message

  &__message {
    @include font-settings(var(--fz-16), 1.2, 400, rgba(255, 255, 255, 0.5), );
    max-width: 510px;

    @include breakpoint(emd) {
      max-width: 100%;
    }

    @include breakpoint(sm) {
      text-align: left;
      font-size: var(--fz-12);
    }
  }

  // .hero__image

  &__image {
    @include breakpoint(sm) {
      width: 100%;
    }
  }

  // .hero__img-wrap

  &__img-wrap {
    display: flex;
    gap: 48px;
    align-items: center;
    position: relative;
    top: 39px;

    @include breakpoint(emd) {
      top: 0;
    }

    @include breakpoint(sm) {
      justify-content: space-around;
      gap: 10px;
    }


    .m-top {
      // margin-top: 78px;

      @include breakpoint(xxl) {
        // margin-top: 50px;
      }

      @include breakpoint(sm) {
        // margin-top: 0;
      }
    }
  }

  // .hero__bg-wrap

  &__bg-wrap {
    background: linear-gradient(180deg, #FFD9EA 0%, #FFFFFF 100%);
    border-radius: 32px;
    position: relative;
    padding: 24px;
    width: 343px;

    @include breakpoint(sm) {
      width: 153px;
      padding: 12px 10px;
      flex: 0 0 auto;
      border-radius: 24px;
    }

  }

  &__bg-wrap-small {
    height: 260px;


    @include breakpoint(xxl) {
      // margin-top: 50px;
    }

    @include breakpoint(sm) {
      margin-top: 0;
      height: 103px;
    }


  }

  &__bg-wrap-large {
    height: 390px;
    // margin-top: 42px;

    @include breakpoint(sm) {
      margin-top: 0;
      height: 140px;
    }
  }

  &__comment-white {
    background: var(--bg-white-color);
  }

  &__comment-pink {
    background: #FF0074;
  }

  &__comment-black {
    background: #101010;
  }

  &__comment {
    color: var(--text-main-color);
    padding: 16px 35px 16px 16px;
    border: 2px solid #ffddec;
    position: absolute;
    bottom: 30px;
    z-index: 9;
    border-radius: 32px 4px 32px 32px;
    max-width: 285px;
    @include font-settings(var(--fz-16), 1.5, 500, );


    @include breakpoint(sm) {
      padding: 8px;
      font-size: 7px;
      border-radius: 22px 2px 22px 22px;
      bottom: 9px;
      left: 8px;
      right: 8px;
    }

    &-pos {
      bottom: -40px;

      @include breakpoint(sm) {
        bottom: -11px;
      }
    }






    &:after {
      content: '';
      width: 8px;
      display: block;
      height: 8px;
      background-image: url(./../images/arrow-comment.svg);
      background-size: cover;
      position: absolute;
      right: 16px;
      bottom: 16px;

      @include breakpoint(sm) {
        width: 5px;
        height: 5px;
        right: 10px;
        bottom: 10px;
      }
    }

  }

  // .hero__picture

  &__picture {
    position: absolute;
    bottom: 0;
    right: 0;

    @include breakpoint(fourk) {
      height: 100%;
    }

    img {
      display: block;
      border-radius: 32px;

      @include breakpoint(fourk) {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      @include breakpoint(sm) {
        width: 124px;
        border-radius: 24px;
      }
    }
  }

  // .hero__name

  &__name {
    @include font-settings(var(--fz-16), 1, 700, var(--darken-color), );

    @include breakpoint(sm) {
      font-size: var(--fz-9);
    }
  }

  // .hero__place

  &__place {
    margin-top: 4px;
    @include font-settings(var(--fz-12), 1, 400, var(--light-grey-color), );

    @include breakpoint(sm) {
      font-size: var(--fz-7);
    }
  }
}




.btn {
  background: var(--bg-btn-color);
  padding: 24px 32px;
  border-radius: 64px;
  border: 1px solid var(--bg-btn-color);

  @include breakpoint(sm) {
    font-size: var(--fz-16);
  }
}