html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: var(--fz-main);
  overflow-x: hidden;
  position: relative;
}



body {
  background: var(--bg-main-color);


  color: var(--text-main-color);
}







* {
  box-sizing: border-box;
}





ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: var(--nav-link-color);
  cursor: pointer;
}

button {
  background: transparent;
  padding: 0;
  border: none;
  outline: none;
  cursor: pointer;
}

h1,
h2,
h3,
p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
}