.tarif {
  background: var(--bg-btn-color);

  border-radius: 0px 0px 64px 64px;
  position: relative;
  z-index: 1;
  margin-top: -90px;
  padding-top: 170px;
  padding-bottom: 128px;


  @include breakpoint(sm) {
    border-radius: 0px 0px 32px 32px;
    padding-bottom: 68px;

  }

  // .tarif__title

  &__title {
    @include font-settings(var(--fz-64), 1.2, 800, var(--white-color), );
    margin-bottom: 96px;
    max-width: 760px;

    @include breakpoint(sm) {
      font-size: var(--fz-32);
      margin-bottom: 48px;
    }
  }

  // .tarif___list

  &__list {
    display: flex;
    justify-content: space-between;
    gap: 48px;

    @include breakpoint(emd) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  // .tarif__item

  &__item {
    background: #db0265;
    border-radius: 32px;

    max-width: 475px;
    width: 100%;

  }

  // .tarif__top-wrap

  &__top-wrap {
    padding: 32px 40px 32px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include breakpoint(sm) {
      padding: 22px 20px 22px 20px;
    }
  }

  // .tarif__name

  &__name {
    @include font-settings(var(--fz-32), 1, 700, var(--white-color), );

  }

  // .tarif__icon

  &__icon {
    span {
      background: #101010;
      opacity: 0.3;
      border-radius: 50%;
      width: 20px;
      height: 20px;
      display: block;
    }
  }

  // .tarif__content-block

  &__content-block {
    background-color: var(--bg-white-color);
    padding: 40px;
    border-radius: 32px;
    height: 617px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include breakpoint(sm) {
      height: 670px;
    }

    @include breakpoint(esm) {
      height: auto;
      padding: 30px 15px;
    }

    li {
      @include font-settings(var(--fz-16), 1, 400, var(--darken-color), );
      padding-bottom: 15px;
      position: relative;
      padding-left: 15px;


      &:before {
        content: '';
        width: 4px;
        height: 4px;
        background-image: url('./../images/dot.svg');
        background-size: cover;
        display: inline-block;
        position: absolute;
        top: 17%;
        left: 0;

      }
    }

    li:last-child {
      padding-bottom: 32px;
    }
  }

  // .tarif__text

  &__text {
    padding-bottom: 26px;
    font-style: italic;
    @include font-settings(var(--fz-16), 1.4, 400, var(--light-grey-color), );
    // font-size/ line-height/ weight/ color
  }

  // .tarif__price

  &__price {
    border-top: 1px solid #C5C5C5;
    padding: 40px 0 0 0;
    @include font-settings(var(--fz-32), 1, 700, #000000, );
    margin-bottom: 40px;
  }

  // .tarif__btn

  &__btn {
    @include font-settings(var(--fz-20), 1, 500, var(--white-color), );
    background: var(--bg-main-color);
    border-radius: 64px;
    padding: 24px 32px;
    display: inline-block;

    @include breakpoint(sm) {
      padding: 18px 26px;
    }
  }

  &__info {}
}