.team {
  color: var(--white-color);
  margin-bottom: 100px;
  // .team__title

  &__title {
    @include font-settings(var(--fz-32), 1, 700, );
    margin-bottom: 63px;
  }

  // .team__wap-block

  &__wap-block {}

  // .team__list

  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(360px, 1fr));

    row-gap: 48px;
    column-gap: 48px;

    @include breakpoint(emd) {
      justify-content: space-around;
    }

    @include breakpoint(sm) {
      grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
    }
  }

  // .team__item

  &__item {
    display: flex;
    align-items: center;
    gap: 24px;

  }

  // .team__img

  &__img {
    display: block;
    width: 96px;
    height: 96px;
    border-radius: 50%;
    overflow: hidden;
    background: var(--white-color);

    @include breakpoint(sm) {
      width: 76px;
      height: 76px;
      flex: 0 0 auto;
    }

    img {
      height: 100%;
      object-fit: cover;
    }
  }

  // .team__description

  &__description {}

  // .team__name

  &__name {
    margin-bottom: 6px;
    @include font-settings(var(--fz-20), 1, 600, );

  }

  // .team__place

  &__place {
    @include font-settings(var(--fz-16), 1, 400, );

  }
}

.container {}