.question {
  padding-bottom: 128px;

  @include breakpoint(emd) {
    padding-bottom: 72px;
  }

  &-container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include breakpoint(emd) {
      flex-direction: column;
      align-items: flex-start;
    }

  }

  // .question__wrap

  &__wrap {}

  // .question__title

  &__title {
    @include font-settings(var(--fz-64), 1, 800, var(--white-color), );
    margin-bottom: 32px;
    max-width: 900px;

    @include breakpoint(sm) {
      font-size: var(--fz-32);
      margin-bottom: 22px;
    }
  }

  // .question__subtitle

  &__subtitle {
    margin-bottom: 32px;
    @include font-settings(var(--fz-20), 1, 600, var(--white-color), );
  }

  // .question__btn

  &__btn {
    display: flex;
    align-items: center;
    gap: 16px;
    @include font-settings(var(--fz-20), 1, 500, var(--white-color), );

    @include breakpoint(sm) {
      font-size: var(--fz-16);
    }
  }
}