@import 'fonts';
@import 'normalize';
@import 'default';
@import 'variables';
@import 'module/header';
@import 'module/hero';
@import 'module/info';
@import 'module/team';
@import 'module/program';
@import 'module/empty';
@import 'module/partners';
@import 'module/question';
@import 'module/footer';




.container {
  max-width: 1592px;
  padding: 0 30px;
  margin: 0 auto;

  @include breakpoint(sm) {
    padding: 0 19px;
  }
}


.content-wrap {
  background: var(--bg-darken-color);
  border-radius: 64px;

  @include breakpoint(emd) {

    border-radius: 32px;
  }
}












.radial {
  width: 79rem;
  height: 55rem;
  right: -26%;
  top: -56%;

  position: absolute;

  background: radial-gradient(circle, rgba(251, 16, 116, 1) 0%, rgba(92, 22, 85, 1) 100%);
  filter: blur(80px);
  z-index: -1;

  @include breakpoint(emd) {
    top: -79%;
    right: -49%;

  }

  @include breakpoint(sm) {
    background: radial-gradient(circle, rgba(251, 16, 116, 1) 0%, rgba(251, 16, 116, 1) 100%);
    width: 90vw;
    height: 70vh;
    right: -20px;
    top: -20px;
  }
}

.radial-bottom {

  width: 60rem;
  height: 13rem;
  left: -20%;
  position: absolute;
  bottom: 0;

  background: radial-gradient(circle, rgba(251, 16, 116, 1) 0%, rgba(251, 16, 116, 1) 100%);
  filter: blur(80px);
  z-index: -1;

  @include breakpoint(sm) {
    width: 60vw;
    height: 45vh;
    left: -20%;
    bottom: 0;
  }

}