.header {
  padding-top: 36px;
  margin-bottom: 47px;
  position: relative;

  &__unstarred {
    display: flex;
    text-align: right;
    align-items: center;
    position: relative;

    span {
      padding-right: 32px;
      max-width: 230px;
      font-family: 'Caveat', sans-serif;
      @include font-settings(var(--fz-28), 1, 400, var(--white-color), );



      @include breakpoint(sm) {
        font-size: var(--fz-16);
        max-width: 100px;
        padding-right: 10px;
      }

      &:before {
        content: '';
        position: absolute;
        background-image: url('../images/line.svg');
        background-size: cover;
        width: 80px;
        height: 6px;

        bottom: 24px;
        left: 19px;


        @include breakpoint(emd) {
          width: 50px;
          bottom: 16px;
          left: 13px;
        }
      }
    }



  }

  &__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include breakpoint(lg) {
      flex-wrap: wrap;
    }

    @include breakpoint(sm) {
      align-items: flex-start;
    }
  }

  // .header__logo

  &__logo {
    max-width: 188px;

    @include breakpoint(sm) {
      width: 125px;
    }
  }



  // .header__arrow

  &__arrow {
    width: 64px;
    height: 64px;
    border: 1px solid #FFFFFF;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;


    @include breakpoint(sm) {
      width: 44px;
      height: 44px;
      align-self: flex-end;
    }

    svg {
      width: 20px;
      height: 20px;

      @include breakpoint(sm) {
        width: 15px;
        height: 15px;
      }
    }
  }
}

.nav {
  flex-basis: 60%;

  @include breakpoint(md) {
    flex-basis: 40%;

  }

  @include breakpoint(emd) {
    order: 3;
    flex-basis: 100%;
  }

  // .nav__list

  &__list {
    display: flex;
    align-items: center;
    gap: 48px;
    justify-content: end;

    @include breakpoint(lg) {
      margin: 35px auto 0;
    }

    @include breakpoint(emd) {
      justify-content: center;
      gap: 20px;
    }
  }

  // .nav__item

  &__item {}

  // .nav__link

  &__link {
    @include font-settings(var(--fz-20), 1, 400, );
    position: relative;
    white-space: nowrap;

    @include breakpoint(sm) {
      font-size: var(--fz-16);
    }

    @include breakpoint(esm) {
      font-size: var(--fz-12);
    }



    &:after {
      content: "";
      display: block;
      position: absolute;
      right: 0;
      bottom: -3px;
      width: 0;
      height: 1px;
      background-color: rgb(255, 255, 255);
      transition: width 0.5s;
    }

    &:hover:after {
      content: "";
      width: 100%;
      display: block;
      position: absolute;
      left: 0;
      bottom: -3px;
      height: 1px;
      background-color: rgb(255, 255, 255);
      transition: width 0.5s;
    }











  }
}